@import url("https://fonts.googleapis.com/css2?family=Sora&display=swap");

body {
  font-family: "Sora", sans-serif;
}

.item-button button {
  margin-top: 20px;
  width: 200px;
  background-color: rgb(140, 248, 140);
  color: black;
}

.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
}

.spinner-border {
  height: 200px;
  width: 200px;
}

.category {
  border: 2px solid gray;
  margin-right: 25px;
  margin-bottom: 10px;
  padding: 10px;
  cursor: pointer;
}

.category img {
  margin-left: 10px;
}

.category-active {
  border: 2px solid blue;
  background-color: rgba(49, 244, 104, 0.501);
}

/* ========== Register Page =========== */

.register {
  display: flex;
  border: 1px solid gray;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}

.register input {
  width: 400px !important;
}

.register-form {
  background-color: rgba(111, 246, 99, 0.41);
  padding: 20px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.register-form h1 {
  text-align: center;
  background-color: rgb(5, 28, 155);
  color: white;
  padding: 10px;
}


.register-form h3 {
  color: rgb(154, 95, 8);
  text-align: center;
}